@font-face {
  font-family: 'Abril_Fatface';
  src: local('Abril_Fatface'), url(./assets//Abril_Fatface/AbrilFatface-Regular.ttf) format('ttf');
}

/* html {
  font-family: 'Abril_Fatface';
} */


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  border-radius: 35px;
}

/* @media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 15s linear;
  }
} */

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav {
  display: flex;
  justify-content: space-evenly;
  background-color: #282c34;
}

.nav-link {
  text-decoration: none;
  color: white;
  padding: 20px;
}

.goBackButton {
  padding: 15px;
  margin: 10px 0 0 15px;
}


